<template>
  <div class="tree-item" @click.stop="expand">
    <div class="tree-item-info">
      <Icon
        :class="{expand:data.expand}"
        v-if="data.children.length!=0"
        size="20"
        type="md-arrow-dropright"
      />

      <Icon v-if="data.type===0" size="20" type="ios-folder-outline"/>
      <Icon size="20" v-if="data.type===1" type="ios-folder-open-outline"/>
      <Icon size="20" v-if="data.type===2" type="ios-settings-outline"/>
      <span>{{data.name}}</span>
      <div class="handleMenu">
        <Button @click.stop="add" v-if="data.type!=2" size="small" type="warning">添加</Button>
        <Button size="small" type="text">修改</Button>
        <Button size="small" type="dashed">删除</Button>
      </div>
    </div>
    <div class="tree-item-children" v-show="data.expand">
      <TreeItem v-for="item in data.children" :key="item.menuId" :data="item"></TreeItem>
    </div>
  </div>
</template>

<script>
export default {
  name: "TreeItem",
  props: {
    data: Object
  },
  methods: {
    expand() {
      this.$set(this.data, "expand", !this.data.expand);
      //   this.data.expand = !this.data.expand;
    },
    add(e) {
      console.log(e);
    }
  }
};
</script>

<style lang='less' scoped>
.tree-item {
  height: auto;
  cursor: pointer;
  padding-left: 25px;

  .tree-item-info {
    height: 30px;
    line-height: 30px;
    user-select: none;
    transform: rotate(0deg);
    transition: all 0.5s;
    cursor: pointer;
    &:hover {
      background: #dddddd;
    }
    .expand {
      transform: rotate(90deg);
      transition: all 0.5s;
      cursor: pointer;
    }
  }
  .handleMenu {
    float: right;
    button {
      margin-right: 5px;
    }
  }
}
</style>